@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
}
.App {
  text-align: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  margin: auto;
}

.spinner-border {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 300px;
  height: 300px;
  border-width: 50px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header {
  width: 100%;
  height: 12%;
  position: relative;
  top: 0;
  background: #F2F6F6;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header h1,
.header h2 {
  margin: 1%;
}

.main {
  width: 100%;
  height: 87%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  justify-items: center;

}

.placeholder {
  font-size: 7vw;
}

.guess {
  width: 20%;
  height: 20%;
}

.btn-container {
  width: 100%;
  height: 100%;
  display: flex;
  width: 40%;
  align-items: center;
  justify-content: space-between;
}

.btn {
  width: 45%;
  height: 35%;
  border-radius: 30px;
  color: #ffffff;
  border: none;
  font-size: 2vw;
  cursor: pointer;
  transition: background-color 300ms ease;
}

.header div .btn:nth-child(2) {
  width: 100%;
}

.description-container {
  width: 70%;
  transition: opacity 300ms ease;
  height: 100%;
  overflow: scroll;
}


.pop-up {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    height: 28vw;
    width: 60vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 5px 5px 15px -5px #000000;
    background: #ffffff;
    border-radius: 15px;
  }

.stat {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3vw;
    font-family: 'Comfortaa', cursive;
}

.stat-label {
    width: 100%;
    border-radius: 15px;
    padding: 6%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    font-size: 1.8vw;
}

.x {
    position: absolute;
    top: 5%;
    right: 3%;
    height: 19%;
    width: 8%;
    cursor: pointer;
}
